.lineBlock {
    width: 40%;
    height: 35px;
    border-radius: 3px;
    background-color: #373737;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .lineInput {
        border: none;
        background-color: #373737;
        width: 86%;
        color: #ffffff;

        
    }

    .lineInput :active, :hover, :focus {
        outline: 0;
        outline-offset: 0;
    }

    .lineButton {
        border: none;
        background-color: #373737;
        width: 10%;
        color: #ffffff;
        font-weight: bold;
        font-size: 20px;
    }
}